import cuCustom from '@/colorui/components/cu-custom.vue'
import mSearch from '@/components/mehaotian-search/mehaotian-search.vue';
export default {
	onLoad() {
		setTimeout(() => {
			this.renderImage = true;
		}, 300);
		uni.request({
			url: 'http://47.104.29.236:9999/api/v1/esclassify',
			method: 'GET',
			data: {},
			success: res => {
				this.esclassify = res.data.data[1]
			},
			fail: () => {},
			complete: () => {}
		});
		this.getShop()
	},
	data() {
		return {
			// 分页和收缩
			queryInfo: {
				pagenum: 1,
				pagesize: 10,
				ishave: false,
				have: "抱歉没有商品了"
			},
			// 控制自定义导航栏是否字体加粗
			isBold: true,

			// 搜索的值
			val3: '',
			// 搜所框提示信息
			placeholder: '动态占位内容',

			// 商品分类
			esclassify: [],
			// 滚动参数
			scrollTop: 0,
			old: {
				scrollTop: 0
			},
			// 判断分类是否选中
			active: null,

			// 商品数据
			shop: [],
			title: 'product-list',
			productList: [],
			renderImage: false
		}
	},
	methods: {
		// 向下滑动到 底部 王海龙改
		lower1(e) {
			this.queryInfo.pagenum = this.queryInfo.pagenum + 1;
			this.getShop();
		},
		getShop(e) {
			console.log(e);
			uni.request({
				url: `http://47.104.29.236:9999/api/v1/shop/${this.active}`,
				method: 'GET',
				data: {
					inputVal: e,
					pagesize: this.queryInfo.pagesize,
					pagenum: this.queryInfo.pagenum
				},
				success: res => {
					if (res.data.data[1].length !== 10 && res.data.data[1].length !== 0) {
						this.shop.push(...res.data.data[1])
					} else if (res.data.data[1].length == 0) {
						return this.queryInfo.ishave = true;
					}
					// console.log(res)
					this.shop.push(...res.data.data[1])
					console.log(this.shop)
				},
				fail: () => {},
				complete: () => {}
			});
		},
		activeClick(index) {
			this.shop = [];
			this.queryInfo.pagenum = 1;
			this.active = index
			this.val3 = "";
			// console.log(this.active)
			// console.log(this.inputVal)
			this.getShop()
		},
		scroll(e) {
			this.old.scrollTop = e.detail.scrollTop
		},
		search(e, val) {
			this['val' + val] = e;
			this.active = null
			console.log(e)
			this.getShop(e)
		},
		// 点击购买
		dianji(shopid) {
			console.log(shopid)
			uni.setStorage({
				key: 'storage_key',
				data: {
					shopid
				},
				success: function() {}
			});
			uni.navigateTo({
				url: '../shopDetail/shopDetail?shopid=' + shopid,
				success: res => {},
				fail: () => {},
				complete: () => {}
			});
		}
	},
	components: {
		cuCustom,
		mSearch
	},
	onPullDownRefresh() {
		this.loadData('refresh');
		// 实际开发中通常是网络请求，加载完数据后就停止。这里仅做演示，加延迟为了体现出效果。
		setTimeout(() => {
			uni.stopPullDownRefresh();
		}, 2000);
	}
}
